import axios from 'axios';
import store from '../../store/index'
export const dashboardController = {
    getCountUser: async function () {
        var count = 0;
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-user',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                count = res.data.data;
            }
        });
        return count;
    },
    getCountProjectActive: async function () {
        var count = 0;
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-projects',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                count = res.data.data;
            }
        });
        return count;
    },
    getCountAreas: async function () {
        var count = 0;
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-areas',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                count = res.data.data;
            }
        });
        return count;
    },
    getCountSection: async function () {
        var count = 0;
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-sections',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                count = res.data.data;
            }
        });
        return count;
    },
    getOffices: async function () {
        // data:{
        // office_turkey: 0,
        // office_syria: 1
        // }
        var data = {};
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-offices',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                data.office_turkey = res.data.office_turkey;
                data.office_syria = res.data.office_syria;
            }
        }).catch(error => {
            console.log(error);
        });
        return data;
    },
    getCountCamping: async function () {
        // data:{
        // active: 0,
        // an_active: 1
        // }
        var data = {};
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + '/count-campings',
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                
                data.active = res.data.active;
                data.an_active = res.data.an_active;
                
            }
        }).catch(error => {
            console.log(error);
        });
        return data;
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}